/**
* @desc: .
*/
function vibrate(pattern) {
  if (navigator) {
    let canVibrate = navigator.vibrate ||
                    navigator.webkitVibrate ||
                    navigator.mozVibrate ||
                    navigator.msVibrate;
  
    if (!canVibrate) {
      if (__debug) {
        console.log("vibrate not supported");
      }
      return;
    }

    navigator.vibrate(pattern);
  }
}

